body {
  margin: 0;
  font-family: 'Arima Madurai', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* NavBar User Menu */
.ant-dropdown-menu-item.ant-dropdown-menu-item-only-child {
  padding: 10px 12px;
}

/* Menu bars */
.ant-layout-sider.ant-layout-sider-dark {
  background: transparent;
}
.ant-layout-header {
  background: transparent;
}

/* Side Menu */
.ant-menu.ant-menu-light.ant-menu-root.ant-menu-inline {
  border-radius: 4px;
}

/* Cart Modal */
.cartModal .ant-modal-title {
  font-size: large;
}
.cartModal .ant-modal-content .ant-modal-close {
  display: block !important;
  margin-top: -10px;
  margin-right: -5px;
}
.cartModal .ant-modal-content .ant-modal-close.focus, 
.cartModal .ant-modal-content .ant-modal-close:focus {
  outline: 0;
  box-shadow: none!important;
}
.cartModal .ant-modal-body {
  padding-top: 0px !important;
  padding-bottom: 0px;
  max-height: 500px;
  overflow-x: auto;
}

/* Mobile Display */
@media (max-width: 500px) {

  /* NavBar */
  .logo span {
    display: none;
  }

  .ant-menu.ant-menu-sub.ant-menu-vertical {
    text-align: center;
  }

  .leftSide {
    flex: 0 0 33.333333% !important;
    max-width: 33.333333% !important;
  }

  .rightSide {
    flex: 0 0 66.666667% !important;
    max-width: 66.666667% !important;
  }

  /* User Setting Menu */
  .ant-dropdown.ant-dropdown-show-arrow.ant-dropdown-placement-bottomCenter {
    left: 250px !important;
    top: 50px !important;
  }

  /* Greetings */
  .navbar .col-6 p {
    font-size: small !important;
    /* margin: 0 !important; */
  }

  .navbar .ant-menu.ant-menu-light.ant-menu-root.ant-menu-horizontal {
    width: 50px;
  }

  .desktopview {
    display: none;
  }

  .mobileview {
    display: block !important;
  }

  /* ProductTypeMenu */
  .ant-layout-sider-children .ant-menu-inline-collapsed.ant-menu-root.ant-menu-inline {
    width: 185px;
  }

  .ant-layout-sider.ant-layout-sider-dark.ant-layout-sider-below {
    z-index: 9;
    min-width: 100vw;
  }

  .ant-layout-sider-zero-width-trigger.ant-layout-sider-zero-width-trigger-left {
    right: 135px !important;
  }

  /* Product List */
  .ant-layout-content .ant-card {
    width: 250px !important;
  }

  /* .bodyContainer .text-center {
    position: absolute;
  } */

  /* Suggestions */
  .suggestions {
    padding-right: 20px;
  }

  .suggestions .ant-row {
    overflow-y: auto;
  }

  .suggestions .ant-row .ant-card {
    width: 120px !important;
  }

  .suggestions .ant-row .ant-card-meta-title {
    font-size: 12px;
  }
}

/* Tablet Display */
@media (max-width: 768px) {
  /* Side Menu */
  .ant-layout-sider-children .col-4.mt-3 {
    padding-left: 0 !important;
  }

  .ant-layout-sider-zero-width-trigger.ant-layout-sider-zero-width-trigger-left {
    right: -76px !important;
    border-radius: 2px !important;
    background: #ffffff !important;
    color: #001529 !important;
  }

  .ant-layout-sider-zero-width-trigger:hover {
    background: #ffffff;
  }

  .bodyContainer main.ant-layout-content.pt-5.pl-5 {
    margin-left: 0px !important;
  }

  .suggestions {
    margin-left: -20px;
  }
}

@media (min-width: 992px) {
  /* Suggestions */
  .suggestions .ant-row .ant-card {
    width: 200px !important;
  }
}